import React, { Component } from 'react';
import {Switch, Route} from 'react-router-dom';
import Homepage from './Homepage';
import TopTracks from './TopTracks/TopTracks';
import TopArtists from './TopArtists/TopArtists';

class Main extends Component {
    render() { 
        var {token} = this.props;
        return (
            <Switch>
                <Route exact path="/" component={Homepage}></Route>
                <Route exact path="/top-tracks" component={TopTracks}></Route>
                <Route exact path="/top-artists" component={TopArtists}></Route>
            </Switch>
            // <Switch>
            //     <Route exact path={`/#access_token=${token}&token_type=Bearer&expires_in=3600`} component={Homepage}></Route>
            //     <Route exact path={`/top-tracks#access_token=${token}&token_type=Bearer&expires_in=3600`} component={TopTracks}></Route>
            //     <Route exact path={`/top-artists#access_token=${token}&token_type=Bearer&expires_in=3600`} component={TopArtists}></Route>
            // </Switch>
        );
    }
}

export default Main;